header {
  text-align: center;
  color: white;
  padding: 20px;
}
header h1 {
  font-size: 53px;
  line-height: 60px;
  font-weight: 200;
  margin:0;
}
button {
  margin-top: 50px;
  background: white;
  color: #161324;
  width: 200px;
  border: 2px solid white;
  height: 50px;
  font-family: 'Lato', sans-serif;
  font-size: 13px;
  letter-spacing: 2px;
  text-decoration: none;
  border-radius: 5px;
  opacity: 0.8;
}
.logo {
  margin: 50px 0 50px 0;
}
.logo span {
  font-family: 'Lato', sans-serif;
  font-size: 13px;
  letter-spacing: 2px;
  vertical-align: middle;
  height: 40px;
  position: relative;
  top: -10px;
  padding: 2px;
}


